$(".burger").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})

$("._show-search").click(function () {
    $(".header").addClass("_open-search")
    $("body").addClass("fixed-body")
    $(window).scrollTop(0);
    $(".header").removeClass("_open")
})

$("._close-search").click(function () {
    $(".header").removeClass("_open-search")
    $("body").removeClass("fixed-body")
})

$(".submenu-spoiler__show").click(function () {
    $(this).parents(".submenu-spoiler").toggleClass("_open")
    $(this).parents(".submenu-spoiler").find(".submenu-spoiler__hidden").slideToggle()

    if($(this).parents(".submenu-spoiler").hasClass("_open")){
        let parent = $(this).parents(".submenu-item").find(".submenu-item__hidden")
        let blockHeight = $(parent).outerHeight()

        $(this).parents(".nav-dropdown__col").css("min-height",`${blockHeight}px`)

        console.log(parent)
        console.log(blockHeight)
    }

})

$(".nav__link").mouseover(function () {
    $(".nav-item").removeClass("_open")
    $("body").removeClass("fixed-body")
    $(".header").removeClass("_show-dropdown")
})

$(".nav-item__show").click(function () {
    if(window.innerWidth>992){
        return
    }

    $(".header").addClass("_show-dropdown")
    $(this).parents(".nav-item").addClass("_open")
    $("body").addClass("fixed-body")
})

$(".nav-item__show").mouseover(function () {
    if(window.innerWidth<=992){
        return
    }
    $(".nav-item").removeClass("_open")
    $(".header").addClass("_show-dropdown")
    $(this).parents(".nav-item").addClass("_open")
    $("body").addClass("fixed-body")
})

$(".submenu-item__show").click(function () {
    if(window.innerWidth>992){
        return
    }

    $(this).parents(".submenu-item").toggleClass("_open")
    $(this).parents(".submenu-item").find(".submenu-item__hidden").slideToggle()
})

$(".submenu-item__show").mouseover(function () {
    if(window.innerWidth<=992){
        return
    }
    $(".submenu-item").removeClass("_open")
    $(this).parents(".submenu-item").addClass("_open")
})

$("._close-dropdown").click(function () {
    $(".header").removeClass("_show-dropdown")
    $(this).parents(".nav-item").removeClass("_open")
    $("body").removeClass("fixed-body")
})

$(document).mouseup( function(e){
    let div = $( ".nav-item" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
            $("body").removeClass("fixed-body")
            $(".header").removeClass("_show-dropdown")
        }
    }
});

